import React from 'react';

var ItemListado = props => {
    return (
        <div className={(props.selected == props.id) ? 'item active' : 'item'} key={props.id}>
            <div className="top">
                <div className="icon" data-url={props.enlace} data-id={props.id} data-subs={props.subs}></div>
                <div className="name">{props.nombre}</div>
            </div>
            <div className="des">{props.descripcion}</div>
        </div>
    );
}

export default ItemListado;