import React from 'react';
import ItemListado from './ItemListado';
import Player from './Player';

let listado = require("../json/playlist.json");


export default class Playlist extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            selected: null,
            listado: null,
            urlPlaying: null,
            subtitulos: null
        }
    }

    componentDidMount() {
        this.setState({
            listado: listado
        });

        let btns = document.getElementsByClassName('item');
        let self = this;
        for(var i=0;i<btns.length;i++) {
            btns[i].addEventListener('click', (e) => {
                const url = e.target.getAttribute('data-url');
                const id = e.target.getAttribute('data-id');
                const subs = e.target.getAttribute('data-subs');


                self.setState({
                    selected: id,
                    urlPlaying: url,
                    subtitulos: subs
                })
            })
        };
    }

    render(){
        let dataListado = [];
        for(let i = 0; i < listado.length; i++){
            dataListado.push(<ItemListado
                nombre={listado[i].nombre}
                descripcion={listado[i].descripcion} 
                enlace={listado[i].enlace}
                subs={listado[i].subtitulos}
                id={i}
                key={i}
                selected={this.state.selected}
                />);
        }

        return (
            <>
            <div className="left">
                <div className="top">
                <img src={process.env.PUBLIC_URL + '/logo-rock.png'} />
                Estas charlas fueron organizadas por la gerencia de música de Idartes como parte de la celebración de los 25 años de Rock al Parque.
                </div>
                {dataListado}
            </div>
            <div className="right">
                <Player url={this.state.urlPlaying} subtitulos={this.state.subtitulos}/>
            </div>
            </>
        );
    }
}